
import { FirebaseDBService } from "@/services/FirebaseDBService";
import { ReportService } from "@/services/ReportService";
import { defineComponent } from "vue";
import { CarReport } from "@/model/CarReport";

export default defineComponent({
  methods: {
    async downloadCSV() {
      const firebaseService = new FirebaseDBService();
      const data = await firebaseService.readData<{ [key: string]: CarReport }>(
        "carReports"
      );
      if (data) {
        const csvContent = new ReportService().reportsToCSV(data);
        this.downloadFile("report.csv", csvContent);
      }
    },
    downloadFile(fileName: string, content: string) {
      const element = document.createElement("a");
      element.setAttribute(
        "href",
        "data:text/plain;charset=utf-8," + encodeURIComponent(content)
      );
      element.setAttribute("download", fileName);
      element.style.display = "none";
      document.body.appendChild(element);
      element.click()
      document.body.removeChild(element);
    },
  },
});
